const getOriAcc = async () => {
  try {
    const response = await axios({
      url: "http://143.89.126.47:8825/test_ori",
      method: "GET"
    })
    return response.data.acc; // Assuming the API returns the 'acc' value in this structure
  } catch (error) {
    console.error("Error fetching acc:", error);
    throw error;
  }
};

const getCompAcc = async () => {
  try {
    const response = await axios({
      url: "http://143.89.126.47:8825/test_comp",
      method: "GET"
    })
    return response.data.acc; // Assuming the API returns the 'acc' value in this structure
  } catch (error) {
    console.error("Error fetching acc:", error);
    throw error;
  }
};

const startSearch = async () => {
  try {
    const response = await axios({
      url: "http://143.89.126.47:8825/search",
      method: "GET"
    })
    return response.data.result; // Assuming the API returns the 'acc' value in this structure
  } catch (error) {
    console.error("Error fetching result:", error);
    throw error;
  }
};

const testComp = async () => {
  try {
    const response = await axios({
      url: "http://143.89.126.47:8825/testcomp",
      method: "GET"
    })
    return response.data.result; // Assuming the API returns the 'acc' value in this structure
  } catch (error) {
    console.error("Error fetching result:", error);
    throw error;
  }
};


const ArticleDetail = () => {
  // const { id } = useParams();
  // const articleIndex = parseInt(id, 10);
  // const article = articles[articleIndex];
  // // const navigate = useNavigate();

  // const prevArticle = articleIndex > 0 ? articles[articleIndex - 1] : null;
  // const nextArticle =
  //   articleIndex < articles.length - 1 ? articles[articleIndex + 1] : null;
  const [oriacc, setOriAcc] = useState(null); // State to hold the 'acc' value
  const [orierror, setOriError] = useState(null); // State to hold any errors
  const [compacc, setCompAcc] = useState(null); // State to hold the 'acc' value
  const [comperror, setCompError] = useState(null); // State to hold any errors
  const [searchres, setSearchRes] = useState(null); // State to hold the 'acc' value
  const [searcherror, setSearchError] = useState(null); // State to hold any errors
  const [testcomp, setTestComp] = useState(null); // State to hold the 'acc' value
  const [testcomperror, setTestCompError] = useState(null); // State to hold any errors

  const handleClickOri = async () => {
    try {
      const fetchedAcc = await getOriAcc();
      setOriAcc(fetchedAcc); // Set the fetched 'acc' value in state
      setOriError(null); // Clear any previous errors
    } catch (error) {
      setOriError('Failed to fetch acc. Please try again.');
    }
  };
  const handleClickComp = async () => {
    try {
      const fetchedAcc = await getCompAcc();
      setCompAcc(fetchedAcc); // Set the fetched 'acc' value in state
      setCompError(null); // Clear any previous errors
    } catch (error) {
      setCompError('Failed to fetch acc. Please try again.');
    }
  };

  const handleClickSearch = async () => {
    try {
      const fetchedResult = await startSearch();
      setSearchRes(fetchedResult); // Set the fetched 'acc' value in state
      setSearchError(null); // Clear any previous errors
    } catch (error) {
      setSearchError('Failed to fetch acc. Please try again.');
    }
  };

  const handleClickTestComp = async () => {
    try {
      const fetchedResult = await testComp();
      setTestComp(fetchedResult); // Set the fetched 'acc' value in state
      setTestCompError(null); // Clear any previous errors
    } catch (error) {
      setTestCompError('Failed to fetch acc. Please try again.');
    }
  };

  return (
    <div className={styles.pageContainer}>
      <Box className={styles.videoBox}>
        <Box className={styles.videoContent}>
          <Typography variant="h3" className={styles.title}>
            {TITLE}
          </Typography>
          {/* <Typography variant="h3" className={styles.subtitle}>
            <strong>{article.conf} </strong>{article.date}
          </Typography> */}
          {/* <Button
            className={styles.dateButton}
          >
            <strong>{article.conf} </strong>{article.date}
          </Button> */}
          
          {/* <a href="https://ijcai20.org/" target="_blank" rel="noopener noreferrer">
            <Button className={styles.dateButton}>
              <strong>{article.conf}</strong> {article.date}
            </Button>
          </a> */}
        </Box>
      </Box>

      <Box className={styles.articleContainer}>
        {/* <Typography variant="h6" gutterBottom className={styles.abstract}>
          <strong>Abstract:</strong> {article.abstract}
        </Typography> */}
        <Typography variant="h6" gutterBottom className={styles.abstract}>
          To date, it is widely acknowledged that model compression methods have been successfully deployed to reduce computations, decrease power demands, and thus enable the deployment of DNNs on low-power devices. Furthermore, it is shown that compressed models can often exhibit overall high test accuracy compared to the original DNN models. <br />
          Nevertheless, our observation shows that the original model Mo and its compressed version Mc (e.g., via pruning), though they may both exhibit high test accuracy, can have deviated prediction outputs on the same input, as illustrated in following figure. We name such inputs as <strong>deviation-triggering (DT) inputs</strong>. We propose <font color="blue"><strong>CompD</strong></font>, the first testing and repairing framework to detect prediction deviations between original models and their compressed counterparts. 
          <img
            src={pic_one}
            alt="Description"
            className={styles.descriptionImage}
          />
          We demonstrate capability of <font color="blue"><strong>CompD</strong></font> using two representative compression schemes: quantization and pruning. Three common DNN models, VGG11, ResNet18, and DenseNet121, trained on CIFAR-10, are selected as our tested models. All the compressed models exhibit similar or even higher accuracy than their original counterparts. However, <font color="blue"><strong>CompD</strong></font> still detects a great number of <strong>deviation-triggering (DT) inputs</strong> with 5000 seed inputs.
          <img
            src={pic_two}
            alt="Description"
            className={styles.descriptionImage2}
          />
          With the <strong>deviation-triggering (DT) inputs</strong> discovered by <font color="blue"><strong>CompD</strong></font>, we can repair Mc. We first collect proper set of <strong>deviation-triggering (DT) inputs</strong> for repairing. Then we utilize this set of inputs to finetune Mc to improve its robustness against <strong>deviation-triggering (DT) inputs</strong>.
          <img
            src={pic_three}
            alt="Description"
            className={styles.descriptionImage2}
          />
        </Typography>
        <button onClick={handleClickOri}>Calculate Accuracy of Original Model</button>
        {oriacc !== null && <div>Acc Value: {oriacc}</div>}
        {orierror && <div style={{ color: 'red' }}>{orierror}</div>}

        <button onClick={handleClickComp}>Calculate Accuracy of Pruned Model</button>
        {compacc !== null && <div>Acc Value: {compacc}</div>}
        {comperror && <div style={{ color: 'red' }}>{orierror}</div>}

        <button onClick={handleClickSearch}>Search for the DTI</button>
        {searchres !== null && <div>Search Status: Complete</div>}
        {searchres == null && <div>Search Status: Not ready yet</div>}
        {searcherror && <div style={{ color: 'red' }}>{searcherror}</div>}

        <button onClick={handleClickTestComp}>Test Effect of DTI on Pruned Model</button>
        {testcomp !== null && <div>Effective DTI: {testcomp}</div>}
        {testcomperror && <div style={{ color: 'red' }}>{testcomperror}</div>}
        {/* <Typography variant="h6" gutterBottom className={styles.abstract}>
          <strong>Abstract: </strong>
          {CONTENT}
        </Typography> */}
        {/* <Typography variant="h7" gutterBottom className={styles.author}>
          <strong>Author: </strong> {article.author} In <strong>{article.conf}</strong>  {article.date}
        </Typography> */}

        {/* <Button
          variant="contained"
          color="primary"
          onClick={() => window.open(article.link, "_blank")}
          className={styles.viewPaperButton}
        >
          View Paper
        </Button> */}

        {/* <Box className={styles.navigationButtons}>
          {prevArticle && (
            <Button
              component={Link}
              to={`/articleDetail/${articleIndex - 1}`}
              className={styles.navButton}
            >
              &lt; Previous
            </Button>
          )}
          {nextArticle && (
            <Button
              component={Link}
              to={`/articleDetail/${articleIndex + 1}`}
              className={styles.navButton}
            >
              Next &gt;
            </Button>
          )}
        </Box> */}
      </Box>
    </div>
  );
};

export default ArticleDetail;
